<template>
  <div class="agentRegistration">
    <div class="mid">
      <div class="card">
        <div class="label">请输入准确的认证信息</div>
        <div class="value">
          <div class="left" @click="upload('1')">
            <img v-if="cardPic1" :src="cardPic1?$utils.dealImg(cardPic1):require('../../assets/img/default/defaultH.png')" alt />
            <div v-else class="choose">
              <div class="picWrap">
                <img src="@/assets/img/icon/upload1.png" alt />
              </div>
              <p class="text">身份证人像面</p>
            </div>
            <input ref="upload1" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
          </div>
          <div class="right" @click="upload('2')">
            <img v-if="cardPic2" :src="cardPic2?$utils.dealImg(cardPic2):require('../../assets/img/default/defaultH.png')" alt />
            <div v-else class="choose" @click="upload('2')">
              <div class="picWrap">
                <img src="@/assets/img/icon/upload1.png" alt />
              </div>
              <p class="text">身份证国徽面</p>
            </div>
            <input ref="upload2" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
          </div>

        </div>
      </div>
      <van-cell-group>
        <van-field v-model="realName" label="真实姓名" placeholder="请输入本人的真实姓名" />
        <van-field v-model="idCardNum" label="身份证号" placeholder="请输入本人的身份证号" />
        <van-field v-model="userMobile" type="number" label="联系电话" placeholder="请输入本人的手机号" />
        <div class="flex" v-if="false">
          <van-field v-model="sms" label="验证码" placeholder="请输入验证码" />
          <div class="smsBtn" @click="getSms">{{min|| '获取验证码'}}</div>
        </div>
      </van-cell-group>
      <div class="agreement">
        <van-checkbox v-model="checked"></van-checkbox>
        <p>
          阅读并同意
          <span >《推广服务协议》《共享经济合作伙伴协议》</span>
        </p>
      </div>
      <div class="btn" @click="save">提交</div>
    </div>
    <van-popup v-model="agreementShow">
      <div class="agreementMid">
        <div class="close" @click="agreementShow=false">×</div>
        <div v-html="agreement">{{agreement}}</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { CellGroup, Cell, Field, Popup, Checkbox } from "vant";
import { uploadPic, saveAgent } from "@/api";
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Checkbox.name]: Checkbox,
  },
  data() {
    return {
      realName: "",
      idCardNum: "",
      userMobile: "",
      cardPic1: "",
      cardPic2: "",
      sms: "",
      checked: true,
      agreementShow: false,
      agreement: "",
      min: 0,
      Interval: null,
    };
  },
  methods: {
    upload(type) {
      this.type = type;
      this.$refs["upload" + type].click();
    },
    chooseUpload(e) {
      let that = this,
        formData = new FormData(),
        files = e.target.files,
        // files = e,
        reader = new FileReader();
      if (files.length < 1) {
        return;
      }
      this.loading = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: "加载中...",
      });
      for (let i = 0; i < files.length; i++) {
        console.log(files[i]);
        let size = files[i].size / 1024 / 1024;
        if (size > 100) {
          // 超过2M
          this.$toast.fail("文件大于100M");
          return;
        }
        reader.readAsDataURL(files[i]);
        reader.onload = function() {
          let content = this.result; //图片的src，base64格式
          let img = new Image();
          img.src = content;
          img.onload = function() {
            //图片加载完毕
            that.compressEvent(img, size, dataURL => {
              that.uploadImg(dataURL, files[i], i, files.length);
            });
          };
        };
      }
    },
    compressEvent(img, size, callback) {
      let that = this,
        canvas = document.createElement("canvas"),
        ctx = canvas.getContext("2d"),
        initSize = img.src.length,
        width = img.width,
        height = img.height;
      // var ratio = this.getPixelRatio(ctx);
      // console.log(width * ratio, height * ratio);
      canvas.width = width;
      canvas.height = height;
      ctx.drawImage(img, 0, 0, width, height);
      let dataURL = canvas.toDataURL("image/jpeg", size > 1 ? 0.1 : 1);
      callback ? callback(dataURL) : null; //调用回调函数
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    dataURItoBlob(dataurl) {
      //dataurl是base64格式
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    uploadImg(dataURL, file, index, length) {
      let that = this,
        blob = this.dataURItoBlob(dataURL),
        formData = new FormData(),
        config = {
          headers: { "Content-Type": "multipart/form-data" },
        };
      formData.append("file", blob, file.name);
      formData.append("path", 1);
      uploadPic(formData, config).then(res => {
        if (index + 1 == length) {
          this.loading.clear();
        }
        if (res.code == 0) {
          that["cardPic" + this.type] = res.data.filename;
          console.log(this.cardPic1);
        } else {
          this.$toast.fail("图片上传失败！");
        }
      });
    },
    getSms() {
      if (!this.userMobile) {
        this.$toast("请输入联系方式");
        return;
      }
      if (!this.$utils.isMobile(this.userMobile)) {
        this.$toast("请输入正确的手机号码");
        return;
      }
      if (!this.min) {
        this.min = 60;
        this.Interval = setInterval(() => {
          if (this.min > 0) {
            this.min--;
          } else {
            clearInterval(this.Interval);
            this.Interval = null;
          }
        }, 1000);
      }
    },
    save() {
      if (!this.cardPic1) {
        this.$toast("请上传身份证正面照");
        return;
      }
      if (!this.cardPic2) {
        this.$toast("请上传身份证反面照");
        return;
      }
      if (!this.realName) {
        this.$toast("请输入真实姓名");
        return;
      }
      if (this.idCardNum && !this.$utils.isIdCard(this.idCardNum)) {
        this.$toast("请输入正确的身份证号");
        return;
      }
      if (!this.userMobile) {
        this.$toast("请输入联系方式");
        return;
      }
      if (!this.$utils.isMobile(this.userMobile)) {
        this.$toast("请输入正确的手机号码");
        return;
      }
      // if (!this.sms) {
      //   this.$toast("请输入验证码");
      //   return;
      // }

      if (!this.checked) {
        this.$toast("请先同意《推广服务协议》《共享经济合作伙伴协议》");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      saveAgent({
        realName: this.realName,
        userMobile: this.userMobile,
        idCardFrontPhoto: this.cardPic1,
        idCardBackPhoto: this.cardPic2,
        idCardNumber: this.idCardNum,
        areaCode: "",
        addrAddr: "",
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("申请已提交");
          setTimeout(() => {
            this.$router.back();
          }, 500);
        } else {
          this.$toast(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = "代理商注册";
  },
  deactivated() {
    clearInterval(this.Interval);
    this.Interval = null;
  },
};
</script>
<style lang="scss" scoped>
.agentRegistration {
  height: 100%;
  .mid {
    background: #fff;
    padding: 0 0 10px;
    .card {
      .label {
        padding: 10px 16px;
        color: #999;
        font-size: 14px;
        line-height: 24px;
        background: #fafafa;
      }
      .value {
        padding: 10px;
        text-align: center;
        display: flex;
        .left {
          margin: 0 10px 0 0;
        }
        .left,
        .right {
          flex: 1;
          img {
            width: 100%;
            border-radius: 10px;
            display: inline-block;
          }
          .choose {
            background: #fceee5;
            border-radius: 10px;
            text-align: center;
            padding: 20px;
            .picWrap {
              width: 60px;
              height: 60px;
              background: #fb9131;
              border-radius: 50%;
              display: inline-block;
              img {
                width: 30px;
                margin: 15px 0;
                display: inline-block;
              }
            }
            .text {
              color: #333;
              line-height: 20px;
              font-size: 12px;
              margin: 10px 0 0;
            }
          }
        }
      }
    }
    .flex {
      display: flex;
      align-items: center;
      padding: 0 16px 0 0;
      .smsBtn {
        background: #fe6263;
        color: #fff;
        font-size: 12px;
        line-height: 30px;
        width: 100px;
        text-align: center;
        margin: 0 0 0 10px;
        border-radius: 5px;
      }
    }
    .agreement {
      padding: 10px 16px;
      display: flex;
      align-items: center;
      font-size: 12px;
      p {
        margin: 0 0 0 5px;
        span {
          color: blue;
        }
      }
    }
    .promise {
      font-size: 10px;
      line-height: 30px;
      text-align: center;
      color: #666;
    }
    .btn {
      margin: 0 10px;
      border: 1pt solid #e24659;
      border-radius: 5px;
      line-height: 40px;
      text-align: center;
      color: #fe6263;
    }
  }
  .van-popup {
    border-radius: 10px;
  }
  .agreementMid {
    width: 85vw;
    padding: 10px;
    height: 85vh;
    overflow-y: auto;
    position: relative;
    .close {
      position: absolute;
      right: 15px;
      top: 5px;
      width: 10px;
      height: 10px;
      color: #999;
      font-size: 20px;
    }
  }
}
</style>
<style lang="scss">
.agentRegistration {
  .van-cell {
    .van-cell__title {
      flex: none;
      width: 90px;
    }
    .van-cell__value {
      // text-align: left;
    }
    .color {
      color: #000;
    }
  }
}
</style>